// Based on https://github.com/jwarby/jekyll-pygments-themes/blob/master/bw.css
.highlight {
  .hll {
    background-color: #ffffcc; }
  .c {
    font-style: italic; }
  .err {
    border: 1px solid #FF0000; }
  .k {
    font-weight: bold; }
  .cm, .c1, .cs, .ge {
    font-style: italic; }
  .gh, .gp, .gs, .gu, .kc, .kd, .kn, .kr {
    font-weight: bold; }
  .s {
    font-style: italic; }
  .nc, .ni, .ne, .nn, .nt, .ow {
    font-weight: bold; }
  .sb, .sc, .sd, .s2 {
    font-style: italic; }
  .se {
    font-weight: bold;
    font-style: italic; }
  .sh {
    font-style: italic; }
  .si {
    font-weight: bold;
    font-style: italic; }
  .sx, .sr, .s1, .ss {
    font-style: italic; } }
